@charset "UTF-8";
@import "reset","variable","mixin","layout","component";
html {height:100%;}

// 테이블 안에 들어가는 이미지 (ex: 유실물 이미지)
.img_tbl {
	max-width:120px;
	max-height:120px;
	width:auto;
    height:auto;
    margin:5px 0;
}

//로그인
.login {
	background-color:$whitegray;
	position:relative;
	height:100%;
}
.login_area {
	width:640px;
	margin:50px 0;
	text-align:center;
	padding:50px;
	background-color:$white;
	position:absolute;
	top:0;
	left:50%;
	transform:translate(-50%, 0);
	h2 {
		display:block;
		text-align:center;
		font-size:24px;
		color:$basicblack;
		font-weight:bold;
		+ section {
			margin-top:30px;
			+ .btns{
				margin-top:50px;
			}
		}
	}
	h3 {
		display:block;
		text-align:center;
		font-size:18px;
		color:$basicblack;
		margin-bottom:15px;
	}
	&.bg {
		overflow:hidden;
		padding:70px;
		width:580px;
		position:absolute;
		top:20%;
		left:50%;
		transform:translate(-50%, -20%);
		&:after {
			display:block;
			content:"";
			position:absolute;
			top:0;
			border-bottom:800px solid $twayred;
			border-left:250px solid transparent;
			border-right:0 solid transparent;
			z-index:0;
			width:800px;
		}
	}
	.bx_login {
		z-index:1;
		padding:30px 35px;
		position:relative;
		border:1px solid $twayred;
		background-color:$white;
		.login_tit {
			overflow:hidden;
			.logo {
				float:left;
				@include bg("/common/logo_r_s1.png", no-repeat, 0 0);
				width:80px;
				height:40px;
			}
			h1 {
				float:right;
				font-size:18px;
				padding-top:7px;
			}
		}
		.cnt_login {
			margin:50px 0 10px 0;
			text-align:left;
		}
		.login_set {
			overflow:hidden;
			a {
				color:$twayred;
			}
		}
	}
	.cnt_login1 {
		margin:50px 0;
		text-align:left;
	}
	.cnt_login2 {
		margin:30px 0;
		text-align:left;
	}
	.subinfo{
		margin-top:5px;
		font-size:13px;
		color:$basicblack;
	}
	.id_ipt {
		box-sizing:border-box;
		border:1px solid $ligntgray;
		padding:0 20px;
		margin:5px 0;
		text-align:left;
		flex-wrap:wrap;
		display:inline-flex;
		li {
			box-sizing:border-box;
			display:inline-flex;
			flex-grow:1;
			flex-direction:column;
			justify-content:center;
			&:first-child {
				padding-right:10px;
				flex-grow:0;
			}
			input {
				height:36px;
				width:100%;
				border:0;
			}
		}
	}
}
.copyright {
	font-size:14px;
	text-align:center;
	position:fixed;
	bottom:10px;
	width:100%;
}