.blind {
	display:block;
	position:absolute;
	top:-2000em;
	left:-2000em;
	text-indent:-2000em;
	width:0;
	height:0;
	font-size:0;
}
.ipt {
	box-sizing:border-box;
	vertical-align:middle;
	border:1px solid $ligntgray;
	padding:2px 8px;
	color:$basicblack;
	font-weight:400;
	display:inline-block;
	+ img {
		margin-top:1px;
		margin-left:3px;
	}
	&:hover {
		border:1px solid #296dca;
	}
	&:active {
		border:1px solid #528def;
	}
	&::-webkit-input-placeholder {color:$gray;}
	&.nor::-ms-input-placeholder {color:$gray;}
	&.size_big{
		height:40px;
		font-size:16px;
	}
	&.size_nor {
		height:36px;
		font-size:14px;
	}
	&.size_sml {
		height:30px;
		font-size:13px;
	}
	&.err {
		border:1px solid $twayred;
		&:active {
			border:1px $darkgray solid;
		}
		&::-webkit-input-placeholder {color:$twayred;}
		&::-ms-input-placeholder {color:$twayred;}
	}
	&:disabled {
		background-color:$whitegray;
		&::-webkit-input-placeholder {color:$gray;}
		&::-ms-input-placeholder {color:$gray;}
	}
	&.suc {
		border:1px $twaygreen solid;
		&::-webkit-input-placeholder {color:$twaygreen; font-weight:500;}
		&::-ms-input-placeholder {color:$twaygreen; font-weight:500;}
	}
}
.lable {
	vertical-align:middle;
	color:$basicblack;
	+ .ipt {
		margin-left:5px;
	}
}
.msg {
	color:$twayred;
	font-size:13px;
	padding:5px 0;
	display:inline-block;
	margin-left:10px;
	&:before {
		content:"!";
		display:inline-block;
		width:17px;
		height:17px;
		text-align:center;
		border-radius:8px;
		vertical-align:middle;
		background-color:$twayred;
		line-height:17px;
		font-size:13px;
		color:$white;
		margin-right:6px;
	}
}
a {
	+ .btn_file {
		margin-left:10px;
	}
}
.btn {
	box-sizing:border-box;
	vertical-align:middle;
	border:1px solid $ligntgray;
	padding:0 10px;
	&:hover {
		background-color:#f4f4f4;
	}
	&.size_big {
		height:40px;
		min-width:152px;
		padding:0 10px;
		font-size:17px;
		font-weight:400;
	}
	&.size_nor {
		height:36px;
		min-width:96px;
		padding:0 10px;
		font-size:16px;
		font-weight:300;
	}
	&.size_sml {
		height:30px;
		min-width:96px;
		padding:0 10px;
		font-size:16px;
		font-weight:300;
	}
	&.btn_red {
		background-color:$twayred;
		border:1px solid $twayred;
		color:$white;
		&:hover {
			background-color:#be2026;
			border:1px solid #be2026;
		}
	}
	&.btn_grn {
		background-color:$twaygreen;
		border:1px solid $twaygreen;
		color:$white; 
		&:hover {
			background-color:#577a2e;
			border:1px solid #577a2e;
		}
	}
	&.btn_blk {
		background-color:$basicblack;
		border:1px solid $basicblack;
		color:$white; 
		&:hover {
			background-color:$black;
			border:1px solid $black;
		}
	}
	&.btn_gry {
		background-color:$darkgray;
		border:1px solid $darkgray;
		color:$white; 
		&:hover {
			background-color:#424242;
			border:1px solid #424242;
		}
	}
	&.btn_lgry {
		background-color:$whitegray;
		border:1px solid $whitegray;
		color:$basicblack;
		&:hover {
			background-color:#eee;
			border:1px solid #eee;
		}
	}
	&.lin_grn {
		border:1px $twaygreen solid;
		color:$twaygreen;
		&:hover {
			background-color:#f4f4f4;
			border:1px solid #577a2e;
		}
	}
	&.lin_red {
		border:1px $twayred solid;
		color:$twayred;
		&:hover {
			background-color:#f4f4f4;
			border:1px solid #be2026;
		}
	}
	&.btn_file {
		height:30px;
		font-size:13px;
		border:1px $darkgray solid;
		&:hover {
			border:1px $darkgray solid;
		}
	}
}

textarea {
	width:100%;
	min-height:200px;
	padding:10px 12px;
	background-color:$white;
	border:1px solid $ligntgray;
	box-sizing:border-box;
	color:$basicblack;
	font-weight:400;
	font-size:13px;
	&:disabled {
		background-color:$whitegray;
		&::-webkit-input-placeholder {color:$gray;}
		&::-ms-input-placeholder {color:$gray;}
	}
}

::-webkit-input-placeholder::after {
    display:inline-block;
    content:"두번째 줄 \A 세번째 줄\A 마지막 줄";
}

::-moz-placeholder { /* Firefox 19+ */
    display:inline-block;
    content:"두번째 줄 \A 세번째 줄\A 마지막 줄";
}

:-ms-input-placeholder {
    display:inline-block;
    content:"두번째 줄 \A 세번째 줄\A 마지막 줄";
}

:-moz-placeholder { /* Firefox 18- */
    display:inline-block;
    content:"두번째 줄 \A 세번째aaa 줄\A 마지막 줄";
}

.paging {
	display:inline-block;
	overflow:hidden;
	vertical-align:middle;
	border:1px solid $ligntgray;
	border-radius:5px; 
	a {
		float:left;
		box-sizing:border-box;
		height:22px;
		padding:3px 8px;
		font-size:13px;
		line-height:1.2;
		color:$basicblack;
		border-left:1px solid $ligntgray;
		&:first-child {
			border-left:0;
		}
	}
	strong {
		float:left;
		box-sizing:border-box;
		height:22px;
		padding:3px 8px;
		font-size:13px;
		line-height:1.2;
		color:$basicblack;
		border-left:1px solid $ligntgray;
		background-color:$ligntgray;
	}
	.prev,.next {
		font-size:17px;
		line-height:0.8;
		color:$ligntgray;
	}
}

h2 {
	position:relative;
	padding-top:5px;
	font-size:24px;
	color:$basicblack;
	&.tit_point {
		&:before {
			content:"";
			display:block;
			position:absolute;
			top:0;
			left:0;
			width:24px;
			height:4px;
			background-color:$twayred;
		}
	}
}

h3 {
	color:$darkgray;
	display:inline-block;
	margin-right:15px;
}
.txt_info {
	display:inline-block;
	font-size:13px;
	margin:9px 10px 0 0;
	strong {
		color:$twayred;
	}
}
.nav_history {
	a {
	    color:$basicblack;
	}
	span {
		&:before {
			content:"";
			border:solid $ligntgray;
			border-width:0 1px 1px 0;
			display:inline-block;
			padding:4px;
			margin:0 10px 0 5px;
			transform:rotate(-45deg);
			-webkit-transform:rotate(-45deg);
		}
		&:first-child {
			&:before {
				display:none;
				margin:0;
			}
		}
	}
}

.wrap_bundle {
	flex-wrap:wrap;
	display:flex;
	.bx_bundle {
		padding-bottom:10px;
		display:flex;
		flex-grow:1;
		flex-direction:column;
		&.custom {
			position:relative;
			padding-right:30px;
			.btns_move {
				position:absolute;
				top:35px;
				right:0;
			}
		}
		.select_mul {
			margin-top:5px;
		}
	}
	.btns_bundle {
		display:flex;
		flex-direction:column;
		justify-content:center;
		margin:0 30px;
		.btn {
			margin:2px 0;
		}
	}
	.btns_move {
		display:flex;
		flex-direction:column;
		width:30px;
		.btn_up {
			height:100px;
			position:relative;
			border:1px solid $ligntgray;
			&:before {
				content:"";
				display:block;
				position:absolute;
				top:50%;
				left:50%;
				transform:translate(-50%, -50%);
				border-left:8px solid transparent;
				border-right:8px solid transparent;
				border-bottom:10px solid $darkgray;
			}
			span {
				position:absolute;
				top:-2000em;
				left:-2000em;
				text-indent:-2000em;
				width:0;
				height:0;
				font-size:0;
			}
			&:hover {
				background-color:$whitegray;
			}
		}
		.btn_down {
			height:100px;
			position:relative;
			border:1px solid $ligntgray;
			&:before {
				content:"";
				display:block;
				position:absolute;
				top:50%;
				left:50%;
				transform:translate(-50%, -50%);
				border-left:8px solid transparent;
				border-right:8px solid transparent;
				border-top:10px solid $darkgray;
			}
			span {
				position:absolute;
				top:-2000em;
				left:-2000em;
				text-indent:-2000em;
				width:0;
				height:0;
				font-size:0;
			}
			&:hover {
				background-color:$whitegray;
			}
		}
	}
}

.select_mul {
	box-sizing:border-box;
	border:1px $ligntgray solid;
	min-width:330px;
	min-height:200px;
}

.tbl {
	thead {
		th {
			height:48px;
			text-align:center;
			font-size:15px;
			color:$basicblack;
			border-top:1px solid $gray;
			border-bottom:1px solid $gray;
			background-color:$whitegray;
		}
	}
	tbody {
		tr {
			&:hover {
				background-color:$whitegray;
				cursor:pointer;
			}
		}
		th {
			border-top:1px solid $gray;
			border-bottom:1px solid $ligntgray;
			background-color:$whitegray;
		}
		td {
			height:38px;
			padding:5px 0;
			text-align:center;
			font-size:14px;
			color:$basicblack;
			line-height:1.5;
			border-bottom:1px solid $ligntgray;
			.ipt.size_sml{
				width:96%;
			}
		}
		td a, td a:hover, td a:active, td a:focus {
			padding-bottom:0.1em;
			color:#2935ca;
			border-bottom:1px solid #2935ca;
		}
	}
	&.nonfocus {
		tbody {
			tr {
				&:hover {
					background-color:transparent;
					cursor:default;
				}
			}
		}
	}
}

