.rtbl {
	display:flex;
	flex-wrap:wrap;
	border-top:1px solid #e5e5e5;
	border-left:1px solid #e5e5e5;
	.rtbl_cell {
		box-sizing:border-box;
		flex-grow:1;
		width:100%;
		padding:10px 5px;
		overflow:hidden;
		text-align:center;
		border-bottom:1px solid #e5e5e5;
		border-right:1px solid #e5e5e5;
		&.cell_head {
			background:$whitegray;
			font-size:16px;
			font-weight:500;
		}
	}
	&.cols2 .rtbl_cell {width:50%;}
	&.cols3 .rtbl_cell {width:33.33%;}
	&.cols4 .rtbl_cell {width:25%;}
	&.cols5 .rtbl_cell {
		width:20%;
		&.in_col2 {width:10%;}
	}
	&.cols6 .rtbl_cell {width:16.6%;}
	&.cols7 .rtbl_cell {width:14.2%;}
	.odr0 {order:0;}
	.odr1 {order:1;}
	.odr2 {order:2;}
	.odr3 {order:3;}
	.odr4 {order:4;}
	.odr5 {order:5;}
	.odr6 {order:6;}
	.odr7 {order:7;}
	.odr8 {order:8;}
	.odr9 {order:9;}
	.odr10 {order:10;}
	@include respond-to(tablet) {
		.rtbl_cell {
			width:calc(100% - 146px) !important;
			text-align:left;
			padding-left:15px;
			order:0;
			&.cell_head {
				width:146px !important;
				text-align:left;
				padding-left:15px;
			}
			&.cell_foot {
				position:relative;
				overflow:visible;
				&:last-child {
					&:after {
						display:none;
					}
				}
				&:after {
					content:"";
					display:block;
					width:calc(100% + 146px);
					position:absolute;
					bottom:-1px;
					left:-146px;
					border-top:1px solid $gray;
				}
			}
		}
	}
}

.select {
	box-sizing:border-box;
	border:1px solid $ligntgray;
	width:150px;
	&.size_big{
		height:40px;
		font-size:16px;
	}
	&.size_nor {
		height:36px;
		font-size:14px;
	}
	&.size_sml {
		height:30px;
		font-size:13px;
	}
	&:disabled {
		background-color:$whitegray;
		color:$gray;
	}
}

.lbl_units {
	margin-left:0;
	.lbl_chk,.lbl_rdio {
		margin:0 50px 5px 0;
	}
	.lbl_unit {
		display:inline-block;
		.lbl_chk,.lbl_rdio {
			margin:0 0 5px 0;
			+ .ipt {
				margin:0 50px 5px -40px;
			}
			+ .radio {
				margin:0 50px 5px -40px;
			}
		}
	}
}
.lbl_wrap {
	overflow:hidden;
	width:100%;
	position:relative;
	.lbl_all {
		position:absolute;
		width:114px;
	}
	.lbl_units {
		margin-left:114px;
		.lbl_chk {
			margin:0 50px 5px 0;
		}
	}
}

.tbl {
	.lbl_units {
		margin-left:0;
		.lbl_chk,.lbl_rdio {
			margin:0 10px 5px 0;
		}
		.lbl_unit {
			display:inline-block;
			.lbl_chk,.lbl_rdio {
				margin:0 0 5px 0;
				+ .ipt {
					margin:0 10px 5px 0px;
				}
				+ .radio {
					margin:0 10px 5px 0px;
				}
			}
		}
	}
}

.chk {
	opacity:0;
	margin-left:-13px;
	+ .lbl_chk{
		padding-left:24px;
		font-size:15px;
		display:inline-block;
		word-break:keep-all;
		min-height:30px;
		padding-top:3px;
		box-sizing:border-box;
		vertical-align:middle;
		@include bg("common/ico_chk.gif", no-repeat, 0px 50%);
	}
	&:checked+ .lbl_chk{
		@include bg("common/ico_chk_on.gif", no-repeat, 0px 50%);
	}
	&:disabled {
		+ .lbl_chk{
			@include bg("common/ico_chk_dis.gif", no-repeat, 0px 50%);
			color:$gray;
		}
		&:checked+.lbl_chk, &.checked+.lbl_chk{
			@include bg("common/ico_chk_on_dis.gif", no-repeat, 0px 50%);
		}
	}
}
.radio{
	display:none;
	+ .lbl_rdio{
		padding-left:24px;
		@include bg("common/ico_radio.png", no-repeat, 0px 50%);
	}
	&:checked+.lbl_rdio, &.checked+.lbl_rdio{
		@include bg("common/ico_radio_on.png", no-repeat, 0px 50%);
	}
	&:disabled {
		+ .lbl_rdio{
			@include bg("common/ico_radio_dis.png", no-repeat, 0px 50%);
			color:$gray;
		}
		&:checked+.lbl_rdio, &.checked+.lbl_rdio{
			@include bg("common/ico_radio_on_dis.png", no-repeat, 0px 50%);
		}
	}
	+.label {
		margin-right:30px;
		+.tool_set {
			margin-left:-27px;
			margin-right:30px;
		}
	}
}

.wrap_add {
	margin-top:10px;
	overflow:hidden;
	&:first-child {
		margin-top:0;
	}
	.add,.del,.reload {
		display:inline-block;
		vertical-align:middle;
		font-size:25px;
		width:30px;
		text-align:center;
		margin-left:5px;
		cursor:pointer;
	}
	.reload {
		font-size:22px;
		color:$darkgray;
	}
	.add { 
		color:$twayred;
	}
	.del {
		color:$twaygreen;
	}
	.add_info {
		display:inline-block;
		margin-left:15px;
		margin-right:15px;
		color:$darkgray;
	}
	.wrap_ipt {
		box-sizing:border-box;
		width:50%;
	}
	.sbj {
		display:inline-block;
		width:100px;
	}
}

