.btn_menu {
	display:block;
	outline:0;
	border:0;
	padding:0;
	overflow:hidden;
	width:17px;
	height:17px;
	cursor:pointer;
	.bars {
		display:block;
		position:relative;
		width:17px;
		height:17px;
		.bar_line {
			display:block;
			position:absolute;
			left:0;
			width:100%;
			height:2px;
			background-color:$twayred;
			border-radius:1px;
			&.line1 {
				top:0px;
			}
			&.line2 {
				top:6px;
			}
			&.line3 {
				top:12px;
			}
		}
	}
} 
header.wrap_gnb {
	position:fixed;
	top:0;
	right:0;
	left:0;
	z-index:1000;
	min-width:$mobile_size;
	font-size:17px;
	.gnbbar {
		position:relative;
		display:flex;
		flex-wrap:wrap;
		align-items:center;
		justify-content:space-between;
		padding:10px 15px;
		background-color:$twayred;
		.brand {
			display:inline-block;
			height:30px;
			padding-left:72px;
			white-space:nowrap;
			line-height:2;
			font-size:0.941rem;
			color:$white;
			@include bg("common/logo_w_s1.png", no-repeat, 0 0);
		}
		.user {
			display:inline-block;
			box-sizing:border-box;
			justify-content:flex-end;
			margin-left:auto;
			text-align:right;
			font-size:0.824rem;
			color:$white;
			strong {
				color:$white;
			}
		}
		.btn_menu {
			visibility:hidden;
			.bar_line {
				background-color:$white;
			}
		}
		.form_btns {
			display:flex;
			align-items:center;
			.btn {
				background-color:$twayred;
				border:1px solid $white;
				font-size:14px;
				color:$white;
				line-height:1.2;
				padding:5px 10px;
				+ .btn {
					margin-left:5px;
				}
				&.btn_white {
					background-color:$white;
					color:$twayred;
				}
			}
		}
	}
	.gnb {
		display:block;
		box-sizing:border-box;
		position:relative;
		top:0;
		left:210px;
		width:calc(100% - 210px);
		word-break:break-all;
		-webkit-transition:all 0.4s linear;
		-moz-transition:all 0.4s linear;
		-ms-transition:all 0.4s linear;
		-o-transition:all 0.4s linear;
		transition:all 0.4s linear;
		a {
			color:$basicblack;
		}
		&.scroll {
			overflow-y:scroll;
		}
		ul.lst_gnb {
			overflow:hidden;
			background-image:linear-gradient(to right, $twayred 100%, rgba(255, 255, 255, 1));
			background-position:0 100%;
			background-size:1px 1px;
			background-repeat:repeat-x;
			background-color:$white;
			li.nav_item {
				float:left;
				text-align:center;
				min-height:50px;
				box-sizing:border-box;
				font-weight:bold;
				&:first-child {
					&:after {
						content:"";
						display:block;
						position:absolute;
						top:54px;
						left:0;
						width:100%;
						height:1px;
						border-top:1px solid $twayred;
					}
				}
				&:last-child {
					position:relative;
					&:after {
						content:"";
						display:block;
						//100% 안먹어서 width:2000px; 적용
						position:absolute;
						top:54px;
						left:0;
						width:2000px;
						height:1px;
						border-top:1px solid $twayred;
					}
				}
				.depth1 {
					position:relative;
					overflow:hidden;
					border-bottom:1px solid $twayred;
					background-color:$white;
					&:before {
						content:'';
						display:block;
						position:absolute;
						bottom:-14px;
						left:50%;
						transform:translate(-50%,-50%);
						border-left:9px solid transparent;
						border-right:9px solid transparent;
						border-bottom:9px solid $twayred;
						-webkit-transition:all 0.1s linear;
						-moz-transition:all 0.1s linear;
						-ms-transition:all 0.1s linear;
						-o-transition:all 0.1s linear;
						transition:all 0.1s linear;
					}
					a {
						display:block;
						padding:14px;
						min-height:54px;
						white-space:nowrap;
					}
				}
				&.active,&:hover {
					.depth1 {
						&:before {
							bottom:-5px;
						}
						a {
							color:$twayred;
						}
					}
				}
				&:last-child {
					.dropdown {
						border-right:0;
					}
				}
			}
		}
		&:hover {
			ul.lst_gnb {
				.dropdown {
					visibility:visible;
					margin:13px 0;
					opacity:1;
				}
			}
		}
		.dropdown {
			border-right:1px solid $ligntgray;
			visibility:hidden;
			height:0;
			-webkit-transition:all 0.2s ease-in;
			-moz-transition:all 0.2s ease-in;
			-ms-transition:all 0.2s ease-in;
			-o-transition:all 0.2s ease-in;
			transition:all 0.2s ease-in;
			opacity:0;
			ul {
				box-sizing:border-box;
				width:100%;
				padding:10px;
				font-weight:normal;
				font-size:0.824rem;
			}
			li {
				margin-top:5px;
				.depth2>a {
					display:block;
					border-radius:5px;
					padding:2px;
					-webkit-transition:all 0.1s linear;
					-moz-transition:all 0.1s linear;
					-ms-transition:all 0.1s linear;
					-o-transition:all 0.1s linear;
					transition:all 0.1s linear;
				}
				&.on,&:hover {
					a {
						background:$twayred;
						color:$white;
					}
				}
			}
		}
		.bx_dpth {
			display:none;
		}
	}
}
.wrap_lnb {
	overflow:hidden;
	position:fixed;
	top:50px;
	right:0;
	left:0;
	bottom:0;
	z-index:1000;
	width:210px;
	background-color:$whitegray;
	border-right:1px solid $ligntgray;
	font-size:16px;
	-webkit-transition:all 0.4s linear;
	-moz-transition:all 0.4s linear;
	-ms-transition:all 0.4s linear;
	-o-transition:all 0.4s linear;
	transition:all 0.4s linear;
	.area_tit {
		background-color:$white;
		height:55px;
		padding:20px;
		font-weight:bold;
		font-size:18px;
		line-height:1.2;
		border-bottom:1px solid $twayred;
		cursor:pointer;
		.btn_menu {
			display:inline-block;
			vertical-align:middle;
			margin-right:8px;
		}
	}
	> .lnb {
		position:absolute;
		top:55px;
		left:0;
		right:0;
		bottom:0;
		overflow-y:auto;
		a {
			color:$basicblack;
			display:block;
		}
		li.nav_item {
			.depth1 {
				position:relative;
				a {
					padding:8px 25px;
				}
				&:before {
					content:"∙";
					display:block;
					position:absolute;
					top:50%;
					left:15px;
					line-height:1;
					color:#aaa;
					transform:translate(0,-50%);
				}
				&:after {
					content:"";
					display:block;
					position:absolute;
					top:50%;
					right:15px;
					line-height:1;
					border-left:6px solid transparent;
					border-right:6px solid transparent;
					border-top:6px solid #aaa;
					transform:translate(0,-50%);
				}
			}
			dl {
				overflow:hidden;
				max-height:0;
				font-size:0.875rem;
				dt {
					margin:7px 0;
					font-weight:bold;
					a {
						padding:3px 25px;
						-webkit-transition:all 0.2s linear;
						-moz-transition:all 0.2s linear;
						-ms-transition:all 0.2s linear;
						-o-transition:all 0.2s linear;
						transition:all 0.2s linear;
					}
					&.on {
						a {
							background-color:$twaygreen;
							color:$white;
						}
					}
				}
				dd {
					position:relative;
					margin:3px 0;
					&:before {
						content:"∟";
						display:block;
						position:absolute;
						top:50%;
						left:30px;
						line-height:1;
						font-size:10px;
						color:$ligntgray;
						transform:translate(0,-60%);
					}
					a {
						padding:2px 20px 2px 40px;
					}
					&.on,&:hover {
						a {
							font-weight:bold;
						}
					}
					&:last-child {
						margin:3px 0 8px 0;
					}
				}
			}
			&.active {
				background-color:$white;
				border-top:1px solid $twaygreen;
				border-bottom:1px solid $twaygreen;
				.depth1 {
					font-weight:bold;
					&:before {
						color:$twaygreen;
					}
					&:after {
						border-bottom:6px solid $twaygreen;
						border-top:0;
					}
				}
				dl {
					max-height:500px;
					-webkit-transition:all 0.7s ease-in;
					-moz-transition:all 0.7s ease-in;
					-ms-transition:all 0.7s ease-in;
					-o-transition:all 0.7s ease-in;
					transition:all 0.7s ease-in;
				}
				&:first-child {
					border-top:0;
				}
			}
		}
	}
}
