@include respond-to(pc) {
	header.wrap_gnb {
		position:relative;
		top:0;
		right:0;
		left:0;
		z-index:1000;
		font-size:17px;
		.gnbbar {
			.btn_menu {
				visibility:visible;
				margin:0 10px;
			}
		}
		.gnb {
			left:0;
			width:100%;
			//display:none;
		}
		.gnb_fold {
			box-sizing:border-box;
			position:relative;
			top:0;
			left:0;
			width:100%;
			overflow:hidden;
			word-break:break-all;
			border-bottom:1px solid $twayred;
			background-color:$whitegray;
			a {
				color:$basicblack;
			}
			li.nav_item {
				a {display:block;}
				.depth1 {
					position:relative;
					a {
						padding:8px 25px;
					}
					&:before {
						content:"∙";
						display:block;
						position:absolute;
						top:50%;
						left:15px;
						line-height:1;
						transform:translate(0,-50%);
						color:#aaa;
					}
					&:after {
						content:"";
						display:block;
						position:absolute;
						top:50%;
						right:15px;
						line-height:1;
						transform:translate(0,-50%);
						border-left:6px solid transparent;
						border-right:6px solid transparent;
						border-top:6px solid #aaa;
					}
				}
				.dropdown {
					overflow:hidden;
					max-height:0;
					font-size:0.875rem;
					background-color:$white;
					li {
						position:relative;
						.depth2 {
							box-sizing:border-box;
							a {
								padding:5px 20px;
								font-weight:bold;
							}
						}
						&.on {
							.depth2 {
								a {
									background-color:$twaygreen;
									color:$white;
									-webkit-transition:all 0.4s linear;
									-moz-transition:all 0.4s linear;
									-ms-transition:all 0.4s linear;
									-o-transition:all 0.4s linear;
									transition:all 0.4s linear;
								}
							}
						}
					}
				}
				&.active {
					.depth1 {
						font-weight:bold;
						background-color:$white;
						a {color:$twaygreen;}
						&:before {
							color:$twaygreen;
						}
						&:after {
							border-bottom:6px solid $twaygreen;
							border-top:0;
						}
					}
					.dropdown {
						max-height:1000px;
						-webkit-transition:max-height 0.7s ease-in;
						-moz-transition:max-height 0.7s ease-in;
						-ms-transition:max-height 0.7s ease-in;
						-o-transition:max-height 0.7s ease-in;
						transition:max-height 0.7s ease-in;
						ul {
							padding-bottom:10px;
							border-bottom:1px solid $twaygreen;
						}
					}
				}
				&:last-child {
					.dropdown ul {
						border-bottom:0;
					}
				}
			}
			.bx_dpth {
				display:block;
				.depth3 {
					position:relative;
					&:before {
						content:"∟";
						display:block;
						position:absolute;
						top:50%;
						left:35px;
						line-height:1;
						font-size:10px;
						color:$ligntgray;
						transform:translate(0,-60%);
					}
					a {
						padding:5px 20px 5px 50px;
					}
					&.on {
						a {
							font-weight:bold;
						}
					}
				}
			}
		}
	}
	.wrap_lnb {
		left:-210px;
	}
	.container {
		.contents {
			left:0;
			width:100%;
		}
	}
}
