/* Tab */
ul.tabs, ul.tabmenu, ul.subtabmenu {
  margin:0px;
  padding:0px;
  list-style:none;
  li {
  	display:inline-block;
  	/*height:40px;*/
 	padding:0px 10px;
 	background-color:$whitegray;
	border:1px solid $ligntgray;
	border-top-left-radius:5px;
	border-top-right-radius:5px;
	border-bottom-left-radius:0px;
	border-bottom-right-radius:0px;
  	cursor:pointer;
	text-align:center;
	line-height:37px;
  	font-size:16px;
	color:$darkgray;
	}
	li.current {
		background-color:$white;
		border-bottom:1px solid $white;
	  	color:$basicblack;
	}
	a {
		display:inline-block;
		vertical-align:middle;
		line-height:24px;
		padding:10px 0;
		color:#333;
	}
	a:hover, a:active, a:focus {
		color:$basicblack !important; 
		border-radius:5px;
	}
}
ul.tabmenu {
	li:nth-child(1):nth-last-child(2), li:nth-child(1):nth-last-child(2) ~ li {
		width:49.5% !important;
		margin-right:0 !important;
	}
	li:nth-child(1):nth-last-child(2) ~ li {
		margin-left:0.5% !important;
	}
}
.tab-content, .tabcontent, .subtabcontent {
		display:none;
		padding:20px;
		font-size:14px;
		color:$darkgray; 
		margin-top:-1px;
	p {
		margin-bottom:10px;
	}
	.result_area {
		width:100%; 
		margin-top:10px;
	}
}
.tab-content.current, .tabcontent.current, .subtabcontent.current {
  display:inherit;
  border:1px solid $ligntgray;
}

.subtabmenu {
	margin-top:30px;
	li {
		min-width:117px;
		font-size:16px;
		margin-top:10px;
	}
}

.tabcontent.current, .subtabcontent.current {
	display:block;
}

.tab.list {
	height:330px;
	ul {
		position:relative;
		margin:0;
		padding:0;
		list-style:none;
		font-size:16px;
		*zoom:1;
		&:after {
			content:"";
			display:block;
			clear:both;
		}
	}
	ul li {
		float:left;
		margin-top:4px;
		margin-right:4px;
	}
	li a.tabmenu {
		position:relative;
		float:left;
		width:137.2px;
		text-decoration:none;
		color:#404040;
		text-align:center;
		border:1px solid #7ca54d;
		border-radius:3px;
		box-sizing:border-box;
		span {
			display:inline-block;
			line-height:35px;
			letter-spacing:-1px;
			cursor:pointer;
		}
	}
	li ul {
		position:absolute;
		top:70px;
		left:0;
		list-style:none;
		width:100%;
		border:0;
		margin:0;
		padding:0;
		*zoom:1;
		&:after {
			content:"";
			display:block;
			clear:both;
		}
	}
	li li a.tabmenu {
		float:none;
		padding:0;
		margin:0;
		border:0 !important;
		color:$darkgray !important;
		font-weight:normal;
		background:transparent;
		letter-spacing:normal;
	}
	li li a.tabmenu:hover, li li a.tabmenu:active, li li a.tabmenu:focus {
		background:#7ca54d;
		color:$white !important;
		text-decoration:underline;
	}
	ul li.active a.tabmenu {
		background:#7ca54d;
		border-radius:3px;
		color:$white;
	}
	li.disable a.tabmenu {
		background:$ligntgray;
		color:#918d8d;
		border:1px solid $ligntgray;
	}
	ul li.common_editor_pc {
		float:left;
		width:62.5%;
		margin-right:0px;
	}
	ul li.common_editor_mobile {
		width:36%;
		float:left;
		margin-left:0.5%;
		margin-right:0px;
	}
	.all_apply_btn, .copy_btn {
		float:right;
		width:70px;
		height:30px;
		font-size:14px;
		color:$white; 
		background:#222;
		margin-left:4px;
	}
	.copy_btn {
		background:$white;
		border:1px solid $ligntgray;
		color:$darkgray;
		box-sizing:border-box;
	}
	.editor_area {
		height:200px;
		margin-top:35px;
		text-align:center;
		background:$ligntgray;
	}
}
