//popup guide
.popup_area {
	.popup_tit {
		width:100%;
		padding:14px 40px;
		background:$twayred;
		position:relative;
		&:before {
			content:"";
			display:inline-block;
			position:absolute;
			top:19px;
			left:40px;
			width:4px;
			height:18px;
			margin-right:14px;
			background:$white;
		}
		h1 {
			padding-left:12px;
			font-size:18px;
			color:$white;
		}
	}
	.popup_cnt {
		padding:0 38px;
		section {
			margin-top:30px;
		}
	}
	.tag {
		display:inline-block;
		padding:2px 4px;
		margin-top:12px;
		font-size:12px;
		background:$ligntgray;
	}
}
.popup_info table.tbl {
	input {
		width:100px;
		text-align:center;
	}
	th {
		font-size:15px;
		background-color:$whitegray;
		border-bottom:1px solid $ligntgray;
	}
}

//노선선택
.row:first-of-type, .row-btn {
	width:100%;
	.btn.btn-block, .btn-up, .btn-down {
		display:block;
	    width:100%;
	    padding:8px 0;
	    background:$ligntgray;
	    margin:5px auto;
	    text-align:center;
	    white-space:nowrap;
	    line-height:1.2;
	    font-size:14px;
	    font-weight:400;
	    border:none;
	    border-radius:3px;
	    box-sizing:border-box;
	    vertical-align:middle;
	}
	.btn-up, .btn-down {
		width:48%;
		height:34px;
		margin:0 1%;
		border:none;
	}
}
.col-sm-5, .col-sm-2, .row-btn button {
    float:left;
}
.col-sm-5 {
    width:38%;
    input[type=text] {
	    margin:-1px 0 -3px 0;
	    vertical-align:middle;
	}
	input[type=text], input[type=password] {
	    width:192px;
	    border:1px solid #c5c7cb;
	    padding:4px 0px 4px 6px;
	    padding:0.1rem 0rem 0.2rem 0.6rem;;
	}
	select[multiple], select[size] {
	    height:auto;
	}
}
.col-sm-2 {
    width:20%;
    padding:0 2%;
}
.form-control {
    display:block;
    width:100%;
    height:34px;
    padding:6px 12px;
    font-size:14px;
    line-height:1.42857143;
    color:$darkgray;
    background-color:$white;
    background-image:none;
    border:1px solid $ligntgray;
    border-radius:4px;
    -webkit-box-shadow:inset 0 1px 1px rgba(0,0,0,.075);
    box-shadow:inset 0 1px 1px rgba(0,0,0,.075);
    -webkit-transition:border-color ease-in-out .15s,-webkit-box-shadow ease-in-out .15s;
    -o-transition:border-color ease-in-out .15s,box-shadow ease-in-out .15s;
    transition:border-color ease-in-out .15s,box-shadow ease-in-out .15s;
	option {
	    font-weight:normal;
	    display:block;
	    white-space:pre;
	    min-height:1.2em;
	    padding:0px 2px 1px;
	}
}

