// 메뉴 접기형 테이블 (ex.어드민관리>그룹권한관리>그룹권한관리(등록))
.menu_tb {
	thead th {
		text-align:center;
		padding-right:12px;
	}
	.depth {
		width:100%;
		border-top:1px solid #e5e5e5;
		float:left;
		&:last-of-type {border-bottom:0;}
		a {
			float:left;
			display:inline-block;
			width:40%;
			margin-top:5px;
			padding:10px 12px;
			color:$basicblack !important;
			text-decoration:none !important;
			box-sizing:border-box;
			position:relative;
			&:hover, &:active, &:focus{
				font-weight:500;
			}
			&:before {
				content:"";
			    display:block;
			    position:absolute;
			    top:50%;
			    left:13px;
			    line-height:1;
			    border-top:0;
			    transform:translate(0, -50%);
			    border-left:6px solid transparent;
			    border-right:6px solid transparent;
			    border-top:6px solid $basicblack;
			}
			&.on:before {
				content:"";
			    display:block;
			    position:absolute;
			    top:14px;
			    left:22px;
			    line-height:1;
			    transform:translate(-50%, 0);
			    border-bottom:6px solid transparent;
			    border-top:6px solid transparent;
			    border-left:6px solid $basicblack;
			    border-right:0;
			}
		}
		ul {
			display:table;
			width:60%;
			li{
				display:table-cell;
				width:10%;
				text-align:center;
				padding:10px 12px;
				&>.chk + .lbl_chk {padding-left:16px;}
				&:nth-of-type(1) {
					width:20%;
				}
			}
		}
		&.depth_all {
			border-top:none;
			a {padding-left:33px;}
		}
		&.depth_01 a {
			padding-left:53px;
			&:before {left:32px;}
			&.on:before {left:40px;}
		}
		&.depth_02 a {
			padding-left:70px;
			&:before {left:50px;}
			&.on:before{left:57px;}
		}
		&.depth_03 {
			display:none;
			a {
				padding-left:72px;
				color:$darkgray;
				font-size:13px;
				&:before {
					display:none !important;
				}
			}
		}
	}
}
