html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, article, aside, hgroup, header, footer, figure, figcaption, nav, section {margin:0; padding:0; border:0; vertical-align:baseline; background:transparent; box-sizing:border-box; font-weight:normal;}
article, aside, header, footer, figure, figcaption, nav, section, div, p, ul, ol, li, dl, dt, dd {display:block;}
body {padding:0; margin:0; box-sizing:border-box; word-break:break-all; -ms-word-break:break-all; font-family:'Noto Sans KR', sans-serif; font-weight:normal; font-size:16px;}
h1, h2, h3, h4, h5, h6 {font-weight:bold;}
ol, ul, li {list-style:none;}
blockquote, q {quotes:none;}
blockquote:before, blockquote:after, q:before, q:after {content:'';}
table {border-collapse:collapse; border-spacing:0; table-layout:fixed; width:100%;}
button {margin:0; padding:0; border:0; font:inherit; color:inherit; background:transparent; overflow:visible; cursor:pointer; line-height:1;}
label {cursor:pointer;}
a {text-decoration:none; cursor:pointer; vertical-align:baseline;}
a:hover {text-decoration:none; cursor:pointer;}
legend, caption {display:none;}
img {border:0; vertical-align:middle;}
address, i, em {font-style:normal;}
strong{color:#cf3232;}


/* form */
textarea {resize:vertical; font:inherit; overflow-y:auto;}
input, select, optgroup {margin:0; padding:0; color:inherit; font:inherit; vertical-align:middle; line-height:normal;}
button, select {text-transform:none;}
button, input[type="button"], input[type="reset"], input[type="submit"] {-webkit-appearance:none; cursor:pointer;}
button:disabled, input:disabled {cursor:default;}
button::-moz-focus-inner,
input::-moz-focus-inner {border:0; padding:0;}
input[type='checkbox'],
input[type="radio"] {padding:0; box-sizing:border-box; cursor:pointer;}
input[type='number']::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {height:auto; -webkit-appearance:none;}
input[type='search'] {-webkit-appearance:none;}
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {-webkit-appearance:none;}
input::-ms-clear {display:none;}
input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration {display:none;}
 

