// chrome scroll bar
nav::-webkit-scrollbar, 
div::-webkit-scrollbar,
ul::-webkit-scrollbar {
  width:5px;
}
nav::-webkit-scrollbar-thumb,
div::-webkit-scrollbar-thumb,
ul::-webkit-scrollbar-thumb {
  background:#aaa;
}
nav::-webkit-scrollbar-track, 
div::-webkit-scrollbar-track,
ul::-webkit-scrollbar-track {
  background:#ddd;
}